body {
  background-color: green;
}

.backdrop {
  width: 100%;
  height: 100%;
  visibility: hidden;
  pointer-events: none;
  background-color: #c8c8c833;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.visible {
  visibility: visible;
  pointer-events: all;
}

.form {
  width: 50%;
  height: 50%;
  background-color: #0ff;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

